import Vue from 'vue'
import Router from 'vue-router'
import Home from '~home/Home'
import Classify from '~classify/Classify'
import MemberCenter from '~member_center/MemberCenter'
import ShoppingCart from '~shopping_cart/ShoppingCart'
import CommodityDetail from '~commodity_detail/CommodityDetail'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/Project/shopping_mall/',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/classify',
      name: 'Classify',
      component: Classify
    },
    {
      path: '/member_center',
      name: 'MemberCenter',
      component: MemberCenter
    },
    {
      path: '/shopping_cart',
      name: 'ShoppingCart',
      component: ShoppingCart
    },
    {
      path: '/commodity_detail/:id',
      name: 'CommodityDetail',
      component: CommodityDetail
    }
  ]
})



