<template>
    <div class="commodity-detail">
        <div class="commodity-detail-head">
            <div class="commodity-detail-wrap" @click="forword"><span class="iconfont head-icon">&#xe669;</span></div>
            <router-link to="/shopping_cart" class="commodity-detail-wrap"><span class="iconfont head-icon">&#xe600;</span></router-link>
        </div>
        <swiper :options="swiperOption">
            <swiper-slide>
                <div class="swiper-commodity">
                    <img class="swiper-commodity-pic" src="//img.alicdn.com/imgextra/i2/3015214310/O1CN01s33Zey1hi1WQoBKQr_!!3015214310.jpg_760x760Q50s50.jpg_.webp" alt="">
                </div>
            </swiper-slide>
            <swiper-slide>
                <div class="swiper-commodity">
                    <img class="swiper-commodity-pic" src="//img.alicdn.com/imgextra/i3/3015214310/O1CN01sbH8Vj1hi1WQ8Chri_!!3015214310.jpg_760x760Q50s50.jpg_.webp" alt="">
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    name: "DetailSwiper",
    data() {
        return {
            swiperOption: {
                loop: true,
                autoplay: 3000,
                autoplayDisableOnInteraction: false,
                pagination: '.swiper-pagination',
                paginationClickable: true,
                paginationType: 'fraction'
            }
        }
    },
    methods: {
        forword() {
            this.$router.go(-1);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .commodity-detail
        &-head
            max_width()
            z-index 3 
            position fixed
            top 0
            left 0
            right 0
            height .9rem
            ^[0]-wrap
                align_vertical()
                width .7rem
                height .7rem
                border-radius 50%
                margin-top .15rem
                background-color rgba(0,0,0,.2)
                .head-icon
                    color #fff
                    font-size .4rem
                &:nth-child(1)
                    float left
                    margin-left .2rem
                &:nth-child(2)
                    float right
                    margin-right .2rem
        & >>> .swiper-commodity
            width 100%
            height 0
            padding-bottom 100%
            &-pic
                width 100%
        & >>> .swiper-pagination
            vertical(.5rem)
            color $white
            width 1.1rem
            left 6.2rem
            border-radius .8rem
            font-size .3rem
            background-color rgba(0,0,0,.2)
</style>



