<template>
    <div class="shopping-cart">
        <van-nav-bar title="购物车" fixed :z-index="2"></van-nav-bar>
        <van-card class="commodity-card border-bottom" 
            :thumb="commodity.commodityLink" 
            :title="commodity.commodityTit" 
            :desc="commodity.commodityDesc" 
            :price="commodity.shoppingPrice" 
            :origin-price="commodity.originPrice"
        >
            <div slot="footer">
                <van-stepper v-model="value" integer class="commodity-step"></van-stepper>
            </div>
        </van-card>
        <recommend :hotGoods="hotGoods"></recommend>
        <footer-nav></footer-nav>
    </div>
</template>

<script>
import Recommend from './components/Recommend'
import FooterNav from '@/common/FooterNav'
export default {
    name: "ShoppingCart",
    data() {
        return {
            commodity: {
                commodityLink: "http://img3.imgtn.bdimg.com/it/u=1043598906,2487637719&fm=26&gp=0.jpg",
                commodityTit: "冬枣",
                commodityDesc: "台湾牛奶脆蜜枣39.8/5斤,新鲜应急水果冬枣",
                shoppingPrice: "14",
                originPrice: "18.3"
            },
            value: 1,
            hotGoods: []
        }
    },
    components: {
        Recommend,
        FooterNav
    },
    methods: {
        init() {
            this.$axios('/Project/shopping_mall/static/mock/home.json').then(this.getHotGoods);
        },
        getHotGoods(res) {
            res = res.data.data;
            this.hotGoods = res.hotGoods;
        }
    },
    mounted() {
        this.init();
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .shopping-cart
        padding-top .8rem
        padding-bottom 1rem
        & >>> .van-nav-bar
            max_width()
            right 0
            vertical(.8rem)
            .van-icon-arrow-left
                &:before
                    font-size .3rem
                    vertical(.68rem)
            .van-nav-bar__text
                font-size .32rem
            .van-nav-bar__title
                font-size .32rem
                font-weight bold
        & >>> .commodity-card
            height 2.7rem
            padding-top .3rem
            margin-top 0
            background-color $white
            position relative
            .van-card__thumb
                align_vertical()
                width 1.8rem
                height 1.8rem
                padding .1rem
                overflow hidden
                border .02rem solid $lightGray
                >img
                    width 100%
            .van-card__content
                height 0
                padding-bottom 26%
                text-align left
                .van-card__title
                .van-card__desc
                    max-height .8rem
                    line-height .4rem
                    margin-top .1rem
                .van-card__title
                    font-size .32rem
                    color $violet
                .van-card__desc
                    font-size .26rem
                .van-card__bottom
                    font-size .32rem
                    .van-card__origin-price
                        font-size .26rem
                        margin-left .2rem
            .van-card__footer
                position absolute
                right .3rem
                bottom .3rem
        & >>> .commodity-step
            .van-stepper__minus
            .van-stepper__plus
            .van-stepper__input
                width .6rem
                height .6rem
                font-size .3rem
            .van-stepper__input
                padding 0
                box-sizing border-box
</style>



